/**
 * This page displays a list of blog entires, which detail the progression of our developement.
 * Maybe we will show them as an outline
 * or maybe they will be cards and the user can click on them to go to more detail.
 */

import React from "react";
import BlogCard from "../components/BlogCard";
// import Timeline from "../components/Timeline";

import { BLOG_ROUTE } from "../resources/routes/frontEndRoutes";

// import images
import blog1image from "../resources/images/blogs/posture-tracking-chris.png";

const Progress = () => {
	return (
		<div style={{ textAlign: "center" }}>
			<h1>Our Journey</h1>
			{blogs.map((blog) => (
				<BlogCard title={blog.title} image={blog.image} text={blog.summary} />
			))}
		</div>
	);
};

export default Progress;

const blogs = [
	{
		title: "Image Processing Script",
		image: blog1image,
		summary:
			"This is the summary of the blog. Kinda like an executive summary, where it tells the user what was built, how it was built, and what was achieved",

		link: BLOG_ROUTE.IMAGE_PROCESSING,
		author: ["Daniel"],
		date: new Date("2020-12-29"),
		label: ["back-end", "image-processing"],
	},
];
